import "../style.css"

import { VideoQuestion, Panel, Modal } from '../../components'
import { initWorkerId, TEXT, jumpToTaskIfAOldUser } from '../../utils'

    ; (async () => {

        initWorkerId()

        const failedModal = new Modal({
            containerId: 'failedModal',
            title: TEXT.rejectPreviousFailedWorkerPanel.title,
            content: TEXT.rejectPreviousFailedWorkerPanel.content,
            onClickButton: () => location.href = '//www.mturk.com/',
            buttonTitle: 'OK',
        })

        await jumpToTaskIfAOldUser(() => failedModal.show());

        // init panel
        const panel = new Panel({
            containerId: 'panelSlot',
            title: TEXT.instruction.title,
            content: TEXT.instruction.content
        })

        const panel2 = new Panel({
            containerId: 'panelSlot2',
            title: `Reminder: ${TEXT.instruction.title}`,
            content: TEXT.instruction.content,
            scroll: true
        })



        // init three video questions
        const videoQuestions = [{
            containerId: 'animation2',
            clipId: 'animation2',
            clipIdx: 0,
            correctAns: ["0"],
            correctAnsReason: 'Reason: The moving squares respresent the concepts of color.',
            hasSubmitBtn: true,
            submitCorrectCallback: () => videoQuestions[1].show() // show next when done
        }, {
            containerId: 'non_animation2',
            clipId: 'non_animation2',
            clipIdx: 1,
            correctAns: ["2"],
            correctAnsReason: 'Reason: The cartoon figures are the point of focus in the clip.',
            hasSubmitBtn: true,
            submitCorrectCallback: () => videoQuestions[2].show() // show next when done
        }, {
            containerId: 'non_animation3',
            clipId: 'non_animation3',
            clipIdx: 2,
            correctAns: ["1"],
            correctAnsReason: 'Reason: There are no graphical designs, only photorealistic images in motion, and a person talking',
            hasSubmitBtn: true,
            submitCorrectCallback: () => {
                document.querySelector<HTMLButtonElement>('#btn_proceed').style.display = 'initial' // show the proceed button
                scrollTo(0, document.body.scrollHeight);
            }
        }].map(options => new VideoQuestion(options))

        videoQuestions  // hide all except the first one
            .filter((d, idx) => idx !== 0)
            .forEach(d => d.hide())


        const instructionModal = new Modal({
            containerId: 'instruction_2',
            title: TEXT.readyToQualificationPanel.title,
            content: TEXT.readyToQualificationPanel.content,
            buttonTitle: 'Confirm!',
            onClickButton: () => location.href = '/qualification.html',
        })

        /**
         * Add event handler to the proceed button
         */
        let step = 0 // define step as 0
        document.querySelector('#btn_proceed')
            .addEventListener('click', () => {
                step += 1 // onClick, increment counter
                console.log(step)
                switch (step) {
                    case 1: {
                        document.querySelector(`#instruction_${step}`).classList.remove('hidden')
                        // hide the proceed button
                        document.querySelector<HTMLButtonElement>('#btn_proceed').style.display = 'none'
                    }
                        break
                    case 2: {
                        instructionModal.show()
                    }
                }
            })

    })();
